<template>
  <div class="floott">
      <div class="floott_box">
        <span class="floott_span" :class="{floott_span_active:isAction==1}" @click="goamapOptions(1)">全部</span>
        <span class="floott_span" :class="{floott_span_active:isAction==2}" @click="goamapOptions(2)">休闲娱乐</span>
        <span class="floott_span" :class="{floott_span_active:isAction==3}" @click="goamapOptions(3)">智慧停车</span>
        <span class="floott_span" :class="{floott_span_active:isAction==4}" @click="goamapOptions(4)">洗手间</span>
        <span class="floott_span" :class="{floott_span_active:isAction==5}" @click="goamapOptions(5)">游客服务</span>
        <span class="floott_span" :class="{floott_span_active:isAction==6}" @click="goamapOptions(6)">出入口</span>
        <span class="floott_span" :class="{floott_span_active:isAction==7}" @click="goamapOptions(7)">智慧管控</span>
        <span class="floott_span"></span>
      </div>

      <!-- <p class="floott_p" @click="goamapOptions(1)" style="margin-left:0.2rem;">
          <img class="index_choise1" src="../assets/imgs/地图tab1_智慧互动1.png" alt="" v-if="isAction==1">
          <img class="index_choise1" src="../assets/imgs/地图tab1_智慧互动2.png" alt="" v-else>
          <span :class="{is_active:isAction==1}">智慧互动</span>
      </p>
      <p class="floott_p" @click="goamapOptions(2)">
          <img class="index_choise2" src="../assets/imgs/地图tab1_智慧管控2.png" alt="" v-if="isAction==2">
          <img class="index_choise2" src="../assets/imgs/地图tab1_智慧管控1.png" alt="" v-else>
          <span :class="{is_active:isAction==2}">智慧管控</span>
      </p>
      <p class="floott_p" @click="goamapOptions(3)">
          <img class="index_choise3" src="../assets/imgs/地图tab1_场馆2.png" alt="" v-if="isAction==3">
          <img class="index_choise3" src="../assets/imgs/地图tab1_场馆1.png" alt="" v-else>
          <span :class="{is_active:isAction==3}">场馆</span>
      </p>
      <p class="floott_p" @click="goamapOptions(4)">
          <img class="index_choise3" src="../assets/imgs/地图tab1_停车2.png" alt="" v-if="isAction==4">
          <img class="index_choise3" src="../assets/imgs/地图tab1_停车1.png" alt="" v-else>
          <span :class="{is_active:isAction==4}">停车</span>
      </p>
      <p class="floott_p" @click="goamapOptions(5)">
          <img class="index_choise3" src="../assets/imgs/地图tab1_洗手间2.png" alt="" v-if="isAction==5">
          <img class="index_choise3" src="../assets/imgs/地图tab1_洗手间1.png" alt="" v-else>
          <span :class="{is_active:isAction==5}">洗手间</span>
      </p> -->

  </div>
</template>
<script>
export default {
  data () {
    return {
      isAction:1
    }
  },
  props:['indexId'],
  watch:{
    indexId(val){//地图传过来的值
      this.isAction = val
    }
  },
  methods:{
    goamapOptions(index){
      this.$nextTick(()=>{
        this.isAction = index
      })
      console.log(index)
      this.$emit('changeBottom',index)
      // this.$router.push({
      //   path:"./"
      // })
    }
  }
}
</script>

<style scoped>
.is_active{
  color:rgb(63, 200, 170);
}
.floott {
    background: #fff;
    width: 100%;
    height:170px;
}
.floott_box{
    color: #de632a;
    font-size: 28px;
    width: 100%;
    display: flex;
    overflow: hidden;
    overflow-x: auto;
    margin-top:30px;
    margin-left:24px;
    padding-bottom:100px;
}
.floott_box::-webkit-scrollbar {
    display: none
}
.floott_span{
  display: block;
  padding:8px 28px;
  flex:0 0 auto;
  /* min-width: 100px; */
  box-sizing: border-box;
  line-height: 100%;
  margin-right:5px;
}
.floott_span_active{
  border:1px solid #f7941d;
  font-weight: 600;
  border-radius: 40px;
}
.floott_p{
  margin-right:0.2rem;
}

.floott_p span {
    display: block;
    text-align: center;
    min-width:110px;
}

.floott img {
    display: block;
    margin: 0 auto;
    width:50px;
    height:50px;
    margin-bottom:10px;
}

</style>



